.gpt3__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem 6rem;
    background-color: #fff;
    position: fixed;
    width: 100%;
    top: 0;
  }
  
  .gpt3__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  
  .gpt3__navbar-links_logo {
    margin-right: 2rem;
  }
  
  .gpt3__navbar-links_logo img {
    width: 62.56px;
    height: 16.02px;
  }
  
  .gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
    margin: auto;
  }
  
  .gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  
  .gpt3__navbar-links_container button,
  .gpt3__navbar-sign button,
  .gpt3__navbar-menu_container p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    margin: 0 1rem;
    cursor: pointer;
    border: none;
    background: none;
  }
  
  .gpt3__navbar-links_container button:hover,
  .gpt3__navbar-sign button:hover{
    background: gray ;
    border-radius: 5px;
    margin: 0 8px;
    padding: 0 0.5rem;
    color: #fff;
  }
  
  .gpt3__navbar-signin button {
    border-radius: 5px;
    border: none;
    line-height: 25px;
    cursor: pointer;
    color: #fff;
    font-weight: 500;
    font-size: 18px;
  }
  
  .gpt3__navbar-signup {
    display: flex;
  }
  
  .gpt3__navbar-signup p,
  .gpt3__navbar-menu_container button {
    padding: 0.1rem 1rem;
    border-radius: 5px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    margin: 0 0.5rem;
  }
  
  
  
  .gpt3__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
  }
  
  .gpt3__navbar-menu svg {
    cursor: pointer;
  }
  
  .gpt3__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
  
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
  }
  
  .gpt3__navbar-menu_container p {
    margin: 1rem 0;
  }
  .gpt3__navbar-menu_container-links-sign {
    display: none;
  }
  
  @media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container {
      display: none;
    }
    .gpt3__navbar-menu {
      display: flex;
    }
  }
  
  @media screen and (max-width: 700px) {
    .gpt3__navbar {
      padding: 2rem 4rem;
    }
  }
  
  @media screen and (max-width: 550px) {
    .gpt3__navbar {
      padding: 2rem;
    }
    .gpt3__navbar-sign {
      display: none;
    }
    .gpt3__navbar-menu_container {
      top: 20px;
    }
    .gpt3__navbar-menu_container-links-sign {
      display: block;
    }
  }
  